@import "./../../helpers/variables";

.section_proposal{
  margin: 50px auto;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.section_proposal.section{
  margin-top: 50px;
}

.proposal_text{
  font-weight: bold;
  font-size: 16px;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.35);
}

.proposal_param{
  margin-top: 8px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.proposal_img{
  margin-right: 6px;
}

.proposal_val{
  font-size: 14px;
  color: $yellow;
  text-shadow: 0 1px 2px rgba(0, 0, 0, .35);
}



.btn_getPWA{
  width: 100%;
  max-width: 340px;
  margin-top: 24px;
  height: 44px;
  line-height: 44px;
  padding: 0 50px;
  transition: .3s ease-in;
  background: linear-gradient(3.38deg, #4F15CA -6.07%, #8515CA 97.35%);
  box-shadow: 0 8px 13px rgba(119, 21, 202, 0.33);
  border-radius: 10px;
  
  &:hover,
  &:focus{
    background: $btn-color-hover;
  }
  
}