
@import "./../Main/helpers/variables";

.header {
  position: fixed;
  top: 0;
  width: 100%;
  min-height: 100px;
  z-index: 5;


  @media (orientation: landscape) and (max-width: 730px) {
    width: calc(100vw - 72px);
  }
  @media (orientation: landscape) and (max-width: 570px) {
    width: calc(100vw - 64px);
  }

}

.header_controls {
  width: 100%;
  height: 55px;

}

.header_controls .container{
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}


.header_userControls{
  background: url("images/bg/header-bg.jpg") 50% 50% no-repeat;
  background-size: cover;
}


.user_control{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}


.user_control__img{
  object-fit: cover;
  margin-right: 8px;
}


.user_control__name{
  position: relative;
  color: #fff;
  font-weight: 500;
  font-size: 15px;
  width: 100%;
  max-width: calc(100vw - 190px);
  text-align: center;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding-right: 5px;
  overflow: hidden;

  text-shadow: 0 0 3px rgba(0, 0, 0, 0.28);
}


///

.header_userInfo{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

}

.coin_block{

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.coin_img{
  position: relative;
  filter: drop-shadow(1px -2px 14px #FF5656), drop-shadow(-3px 2px 4px rgba(0, 0, 0, 0.25));
  z-index: 2;
}


.coin_value{
  position: relative;
  padding-left: 10px;
  padding-right: 20px;
  min-width: 50px;
  display: inline-block;


  font-weight: 600;
  font-size: 12px;
  color: #FFD600;
  z-index: 1;
  &:before{
    content: '';
    position: absolute;
    top: -2px;
    left: -10px;
    width: 100%;
    height: 20px;
    border-radius: 72px;
    background: #000;
    opacity: 0.3;
    z-index: -1;
  }

  @media (min-width:860px) {
    min-width: 100px;
  }


}



.btn_navigation{
  width: 40px;
  height: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
}




//

.header_authorization{
  background-color: #261B44;
}

.btn_enter {
  width: calc(100% - 236px);
  margin-left: 0;
  filter: drop-shadow(0px 8px 13px rgba(119, 21, 202, 0.33));
  background: $btn-purple;
  &:after{
    content: "";
    position: absolute;
    left: 3px;
    top: 2px;
    width: calc(100% - 6px);
    height: 40px;
    z-index: 1;
    background: #261B44;
    border-radius: 10px;
  }
  @media (min-width:390px) {
    width: 100%;
  }

  @media (min-width:500px) {
    width: auto;
  }

}

.btn_enter > span{
  position: relative;
  z-index: 2;
}

.btn_registration {
  margin-right: 0;
  width: 100%;
  padding: 0 10px;
  max-width: 216px;
  background: $btn-purple;
  box-shadow: 0 8px 13px rgba(119, 21, 202, .33);
}




