@import "./../../Main/helpers/variables";

.game_block{
  position: relative;
  margin-top: 24px;
}

.game_block__top{
  position: relative;
  width: 100%;
  border-radius: 16px;
  border:1px solid #C5B7FF;
  text-align: center;
  &:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(17, 0, 38, .7);
  }
  overflow: hidden;
}

.game_block_navigation{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  z-index: 3;
}

.game_name{
  font-weight: bold;
  font-size: 20px;
  line-height: 26px;

  @media (min-width:370px) {
    font-size: 26px;
    line-height: 31px;
  }
}
.game_platform{
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  color: $yellow;
}

.btn_playGame{
  margin-top: 20px;

  background: $btn-purple;
  height: 42px;
  line-height: 42px;
  border-radius: 10px;
  &:hover{
    background: $btn-color-hover;
  }

  @media (min-width:390px) {
    margin-top: 56px;
  }

}

.game_info{
  margin: 32px auto 40px;
  padding: 20px 23px;

  background:  linear-gradient(180deg, rgba(78, 21, 49, .9) 0%, rgba(1, 0, 65, .9) 100%),
               linear-gradient(90deg, #5A089A 0%, #DDB1FF 50%, #6A1CA7);
  background-origin: padding-box, border-box;
  background-repeat: no-repeat;
  border: 1px solid transparent;
  border-radius: 8px;

  @media (min-width:991px) {
    border-radius: 16px;
    padding: 32px 30px;
  }

}

.game_info__title{
  margin-bottom: 30px;
  text-align: center;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;

  @media (min-width:991px) {
    font-size: 24px;
    line-height: 30px;
  }

}


.game_info__item{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  &:not(:last-child){
  margin-bottom: 32px;
  }
}

.game_info__item span:nth-child(1){
  font-size: 14px;
}

.game_info__item span:nth-child(2){
  font-weight: bold;
  font-size: 14px;
  color: $yellow;
}

.game_seo{
  margin: 40px auto;
}
.game_seo p {
  font-size: 14px;
  color: #D1D0E5;
  margin-bottom: 15px;
}
.game_seo a{
  color: $yellow;
}