@import "../helpers/variables";

.section{
  margin-top: 20px;
}



.section_title{
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;

  @media (min-width:450px) {
    font-size: 24px;
    line-height: 26px;
  }

  @media (min-width:600px) {
    font-size: 26px;
    line-height: 28px;
  }
}