.menu {
  position: fixed;
  right: 0;
  bottom: 0;
  z-index: 10;
  width: 100%;


  @media (orientation: landscape) and (min-height: 370px) {
    width: initial;
  }

  @media (min-width: 860px) {
    width: initial;
  }

  @media (orientation: landscape) and (max-width: 570px){
    width: initial;
  }

}

.menu_list {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  height: 55px;
  background: linear-gradient(180deg, rgba(78, 21, 49, .9) 0%, rgba(1, 0, 65, .9) 100%),
  linear-gradient(0deg, rgba(0, 0, 0, .25), rgba(0, 0, 0, .25)),
  linear-gradient(180deg, #7032AE 0%, #4B32AE 100%);
  box-shadow: 0px -4px 10px rgba(0, 0, 0, 0.45);
  backdrop-filter: blur(20px);

  @media (orientation: landscape) and (min-height: 300px) {
    height: 100vh;
    width: 72px;
    flex-direction: column;
  }

  @media (min-width: 860px) {
    height: 100vh;
    width: 72px;
    flex-direction: column;
  }

}

.menu_item {
  position: relative;
  min-width: 64px;
  height: 55px;
  padding-bottom: 2px;

  @media (min-width: 360px) {
    min-width: 72px;
  }

  @media (orientation: landscape) and (min-height: 300px) {
    margin: 10px 0;
  }

  @media (min-width: 860px) {
    margin: 20px 0;
  }

  @media (orientation: landscape) and (max-width: 570px) {  /// Iphone 5 gorizontal
    min-width: 60px;
    margin: 0 0 5px;
  }

}

.menu_link {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

.menu_link.active {
  position: absolute;
  top: -6px;
  left: 0;
  padding-top: 6px;
  //height: 61px;
  width: 72px;
  text-align: center;
  background: linear-gradient(180deg, #49154E 0%, #3D0080 100%);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.28);
  border-radius: 4px;

  .menu_name {
    color: #fff;
    font-weight: bold;
  }
}

.menu_name {
  color: #D1D0E5;
  font-size: 12px;
  @media (min-width: 360px) {
    font-size: 14px;
  }
}

