body {
    position: relative;
    font-family: "Fira Sans", sans-serif;
    font-size: 15px;
    line-height: 1.4;
    font-weight: 400;
    min-width: 320px;
    display: -webkit-box;
    display: flex;
    -webkit-box-orient: vertical;
    flex-direction: column;
    color: #fff;
    min-height: 100vh;
    background: #0c0c0c;
    background-blend-mode: overlay;
}


.noScroll {
    height: 100vh;
    overflow: hidden;
}

img {
    max-width: 100%;
    height: auto;
}

figure {
    margin: 0;
}

a {
    text-decoration: none;
    color: #fff;
}
a:hover,
a:focus {
    text-decoration: none;
    color: #fff;
}



p {
    margin: 0;
    padding: 0;
}

ul, ol {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

button {
    border: none;
    padding: 0;
    margin: 0;
    cursor: pointer;
    background: transparent;
    outline: none;
}


input:focus::placeholder,
textarea:focus::placeholder {
    opacity: 0;
}
input::placeholder,
textarea::placeholder {
    opacity: 1;
    transition: opacity .3s;
}

input::placeholder::-moz-focus-outer,
textarea::placeholder::-moz-focus-outer,
input::placeholder::-moz-focus-inner,
textarea::placeholder::-moz-focus-inner {
    border: 0;
    outline: none;
}


.container {
    display: block;
    margin: 0 auto;
    width: 96%;
    box-sizing: border-box;
    //border-left: 1px solid #fff;
    //border-right: 1px solid #fff;
    max-width: 1620px;

    @media (max-width: 1800px){
        max-width: 1420px;
    }

    @media (max-width: 1520px){
        max-width: 1260px;
    }

    @media (max-width: 1490px){
        max-width: 1120px;
    }

    @media (max-width: 1280px){
        max-width: 970px;
    }
    @media (max-width: 1120px){
        max-width: 850px;
    }

    @media (max-width: 991px){
        max-width: 780px;
    }

    @media (max-width: 900px){
        max-width: 700px;
    }

    @media (max-width: 768px){
        max-width: 600px;
    }
    @media (max-width: 490px){
        max-width: initial;
        width: calc(100% - 20px);
        padding: 0;
    }
}

