h1 {
    color: #fff3e0;
}

.hidden {
    display: none !important;
}


.btn{
    position: relative;
    margin: 5px 10px;
    min-width: 114px;
    font-weight: bold;
    font-size: 14px;
    text-transform: uppercase;
    color: #fff;
    height: 44px;
    border-radius: 10px;
}




.wrapperBlock {

    display: -webkit-box;
    display: flex;

    -webkit-box-orient: vertical;
    flex-direction: column;


    width: 100%;
    max-width: 100vw;
    height: 100%;
    min-height: 100vh;
    justify-content: space-between;

}

::-webkit-scrollbar {
    width: 5px;
}

::-webkit-scrollbar-thumb {
    border-width: 1px 1px 1px 2px;
    border-color: #777;
    background-color:#4F15CA;
}


::-webkit-scrollbar-track-piece {
    background-color: #181A25;
}



