//////  POPUPS
@import "../Main/helpers/variables";

.ShadowBlock {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  background-color: rgba(22, 0, 50, .85);
  z-index: 15;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}


.popupInner {
  position: relative;
  padding: 32px 30px;

  background: linear-gradient(rgb(51, 18, 66), rgb(51, 18, 66)),
              linear-gradient(90deg, #5A089A 0%, #DDB1FF 50%, #6A1CA7);
  background-origin: padding-box, border-box;
  background-repeat: no-repeat;
  border: 1px solid transparent;


  border-radius: 8px;
}


.btn_closePopups {
  position: absolute;
  right: -20px;
  top: -20px;
  width: 40px;
  height: 40px;

  &:before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 24px;
    height: 24px;
    background-color: #fff;
    border-radius: 50%;
    z-index: 1;
  }
}

.btn_closePopups__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;

}


///// Popup INFO

.item_popup {
  position: relative;
  text-align: left;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  &:not(:last-child) {
    margin-bottom: 26px;
  }
}

.item_popup:hover .item_popup_name{
  text-decoration: underline;
}


.popup_info__inner {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

}

.item_popup__img{
  margin-right: 10px;
}
