@import "./../../helpers/variables";


.slider_all {
  position: relative;
  margin: 15px auto 0;
  //overflow: hidden;
}


.slider_list {

  display: flex;
  align-items: center;
  transition: .2s ease-in-out;

  transform: translate3d(0px, 0px, 0px);
  user-select: none;
  touch-action: pan-y;
}


.slider_item {
  position: relative;
  border-radius: 16px;
  border: 1px solid #594B73;
  box-sizing: border-box;

  margin-right: 5px;

  overflow: hidden;

  &:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(17, 0, 38, .7);
  }
  @media (min-width: 360px) {
    margin-right: 10px;
  }

  @media (min-width: 390px) {
    margin-right: 20px;
  }

}


.slider_item img {
  pointer-events: none;
}

.slider_img {
  display: block;
  width: auto;
  border-radius: 16px;
}

.slider_wrapper {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  z-index: 2;
}

.slider_dat {
  display: block;
  padding: 1px 20px;
  background: rgba(255, 255, 255, 0.2);
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;

  @media (min-width: 490px) {
    padding: 5px 24px;
  }

}

.slider_name {
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
}

.slider_center {
  margin: 5px auto 0;
  @media (min-width: 400px) {
    margin: 15px auto;
  }

  @media (min-width: 490px) {
    margin: 20px auto;
  }

}

.slider_text {
  font-size: 14px;
  line-height: 17px;
}

.slider_value {
  margin-top: 7px;
  font-weight: 900;
  font-size: 30px;
  line-height: 32px;
  color: $yellow;

  @media (min-width:360px) {
    font-size: 40px;
    line-height: 48px;
  }
}


.link_tournament {
  display: block;
  line-height: 30px;
  height: 30px;
  background: linear-gradient(90deg, #2FAC3C 3.12%, #1DB56C 99.22%);

  padding: 0 22px;
  font-size: 16px;
  transition: .2s ease-in;
  border-radius: 15px;

  &:hover,
  &:focus {
    transition: .2s ease-in;
    background: linear-gradient(180deg, #F27E3D 0%, #FF5372 94.27%);
  }

  @media (min-width:360px) {
    line-height: 42px;
    height: 42px;
    font-size: 18px;
  }

  @media (min-width: 490px) {
    padding: 0 30px;
    font-size: 20px;
  }

}


.timer {
  width: 100%;
  background: rgba(255, 255, 255, .1);
}

.timer_block {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 5px;

  @media (min-width: 490px) {
    padding: 10px 15px;
  }
}

.timer_rank {
  width: 100%;
  height: 8px;
  border-radius: 8px;


}

.timer_rank__line {
  height: 8px;
  background: linear-gradient(91.4deg, #FFF -98.3%, #AFE2FF 99.4%);
  box-shadow: 0 0 7px #00B2FF,
  0 0 6px rgba(0, 0, 0, .4);
  border-bottom-left-radius: 16px;
}


.timer_name,
.timer_value{
  font-size: 12px;

  @media (min-width: 390px){
    font-size: 15px;
  }
}

