@import "helpers/variables";

.main_content {
  padding-top: 110px;
  position: relative;
  padding-bottom: 70px;
  @media (min-width:860px) {
   padding-bottom: 0;
  }

  @media (orientation: landscape) and (max-width: 730px) {
    width: calc(100vw - 72px);
  }
  @media (orientation: landscape) and (max-width: 570px) {
    width: calc(100vw - 64px);
  }
}



